import { getKeyString } from './lib/keys'
import Tracklist from './Tracklist'

export default function TracklistEditor({ tracks, tracklist, setTracklist }) {
  return <div>
    <textarea className="tracklist" onChange={e => setTracklist(e.target.value)}>
      {tracklist}
    </textarea>
    <Tracklist tracks={tracks} />
  </div>
}
